@import '../../global.scss';

.portfolioFeatured{
	background-color:$wColor;

	h1{
		text-align:center;
		font-size:2rem;
		margin-top:10px;
		
		@include mobile768{
			font-size:20px;
		}
		@include mobile280{
			font-size:1.25rem;
		}
	}
	
	.btn{
		text-align:center;
	}
	
	.box{
		padding:20px;
		border-radius:5px;
		display:flex;
		width:90%;
		margin:15px auto;
		flex-direction:column;
		
		@include mobile1280{
			margin-top:0px;
		}
		
		.imgBxContainer{
			background-color:red;
			width:100%;
			height:100%;
			overflow:hidden;
			display:flex;
			align-items:center;
			justify-content:center;
			flex-wrap:wrap;

			@include mobile1280{
				width:70%;
				height:70%;
				margin-left:auto;
				margin-right:auto;
			}
			@include mobile280{
				width:105%;
				height:105%;
				margin-left:auto;
				margin-right:auto;
			}

			img{
				margin:15px;
				flex:.4;
				width:100%;
				height:100%;
				object-fit:cover;
				border:1px solid $darkColor;
				box-shadow:0 5px 15px rgba(0,0,0,0.5);
				transition:all 1s ease;

				
				&:hover{
					transform:scale(1.1);
				}
			}

		}

		

	}
}