@import '../../global.scss';

.menu{
	width:400px;
	height:100vh;
	background-color:$mainColor;
	position:fixed;
	top:0;
	right:-400px;
	z-index:2;
	padding:50px;
	display:flex;
	flex-direction:column;
	align-items:center;
	justify-content:center;
	transition:all 1s ease;



	@include mobile1024{
		width:60vw;
		height:100vh;
		right:-60vw;
		padding:150px;
		font-size:1.2rem;
	}	
	@include mobile768{
		width:100vw;
		right:-100vw;
		padding:150px;
	}
	@include mobile414{
	
	}		
	@include mobile411{
	
	}	
	@include mobile375{
	
	}	
	@include mobile360{

	}	
	@include mobile320{
	
	}	
	@include mobile280{
		width:380px;
		height:100vh;	
		right:-380px;
	}
	
	&.active{
		right:0;
	}
	
	ul{
		margin:0;
		padding:0;
		list-style:none;
		font-size:30px;
		font-weight:500;
		color:$mainWColor;
		width:65%;


		@include mobile1024{
			font-size:2rem;
			font-weight:500;
			overflow:hidden;
		}	
		@include mobile768{
			font-size:2.5rem;
			font-weight:500;
		}		
		@include mobile540{
			width:280px;
			font-size:1.8rem;
			font-weight:500;
			padding-left:4.5rem;	
		}
		@include mobile415{
			width:280px;
			font-size:1.8rem;
			font-weight:500;
			padding-left:4.5rem;	
		}
		@include mobile411{
			width:280px;
			/*background-color:red;*/
			font-size:1.8rem;
			font-weight:500;
			padding-left:4.5rem;				
		}
		@include mobile360{
			width:280px;
			/*background-color:red;*/
			font-size:25px;
			font-weight:500;
			padding-left:5rem;		
		}
		@include mobile320{
			width:280px;
			/*background-color:red;*/
			font-size:25px;
			font-weight:500;
			padding-left:5rem;		
			overflow:hidden;
		}
		@include mobile280{
			width:280px;
			font-size:22px;
			font-weight:500;
			padding-left:8rem;
		}

		
		li{
			margin-bottom:50px;
			padding-left:10px;
			
			a{
				font-size:inherit;
				color:inherit;
				text-decoration:none;
			}
			&:hover{
				font-weight:700;
			}
		}
	}
}