@import '../../global.scss';

.navbar{
	width:100%;
	height:70px;
	background-color: $wColor;
	color: $mainColor;
	position:fixed;
	top:0;
	z-index:10;
	transition:all 1s ease;
	overflow:hidden;
	
	
	.wrapper{
		padding:10px 30px;
		display:flex;
		justify-content:space-between;
		align-items:center;
		
		.left{
			display:flex;
			align-items:center;
			margin-top:10px;
			
			.logo{
				font-size:25px;
				font-weight:700;
				text-decoration:none;
				color:inherit;
				margin-right:40px;
				

				@include mobile280{
					font-size:18px;
					margin-top:0px;
				}
				
				.fa-react{
					font-weight:500;
					margin-right:0.25rem;
					font-size:1.25rem;
						
					@include mobile280{
						font-size:18px;
						margin-top:0px;
					}
					
				}
			}
			.itemContainer{
				display:flex;
				align-items:center;
				margin-left:30px;
				
				@include mobile768{
					display:none;
				}

				.icon{
					font-size:18px;
					margin-right:5px;
				}
				
				span{
					font-size:15px;
					font-weight:00;
				}
			}
		
		}
		
		.right{
			.hamburger{
				width:32px;
				height:25px;
				display:flex;
				flex-direction:column;
				justify-content:space-between;
				cursor:pointer;
				
				span{
					width:100%;
					height:3px;
					background-color:$mainColor;
					transform-origin:left;
					transition:all 2s ease;
				}
			}
		}
	}
	
	&.active{
		background-color:$mainColor;
		color:$mainWColor;
		
		
		.hamburger{
			span{
				&:first-child{
					background-color:$wColor;
					transform:rotate(45deg);
				}
				&:nth-child(2){
					opacity:0;
				}
				&:last-child{
					background-color:$wColor;
					transform:rotate(-45deg);
				}
			}
		}
		
	}
	
}