@import '../../global.scss';

.about{
	background-color:$wColor;
	overflow:hidden;
	
	h1{
		font-size:2rem;
		text-align:center;
		color:$darkColor;
		margin-top:2rem;
		margin-bottom:2.5rem;
		
		@include mobile540{
			font-size:1.7rem;		
			margin-bottom:1.5rem;			
		}
		@include mobile414{
			font-size:1.5rem;		
			margin-bottom:1.5rem;			
		}
		@include mobile411{
			font-size:1.5rem;		
			margin-bottom:1.5rem;			
		}
		@include mobile360{
			font-size:1.5rem;		
			margin-bottom:1.5rem;			
		}
		@include mobile320{
			font-size:1.2rem;		
			margin-bottom:.7rem;			
		}
	}
	
	.aboutWrapper{
		display:flex;
		margin-bottom:20px;

		@include mobile1024{
			margin-top:-15px;		
		}
		@include mobile768{			
			flex-direction:column;
			align-items:center;
			justify-content:center;
		}


		.left{
			flex:.5;
			margin-left:auto;
			margin-right:auto;
			overflow:hidden;
			
			.imgContainer{
				width:100%;
				height:100%;
				display:flex;
				align-items:center;
				float:right;
				overflow:hidden;

				@include mobile768{
					width:600px;
					height:340px;
					border-radius:0;
					align-items:flex-start;
					float:none;
				}
				@include mobile540{
					width:440px;
					height:240px;
					border-radius:0;
					align-items:flex-start;
					float:none;
					
					img{
						width:100%;
						height:100%;
						object-fit:cover;
					}		
				}
				@include mobile414{
					width:320px;
					height:180px;
					border-radius:0;
					align-items:flex-start;
					float:none;
					
					img{
						width:100%;
						height:100%;
						object-fit:cover;
					}		
				}
				@include mobile411{
					width:320px;
					height:180px;
					border-radius:0;
					align-items:flex-start;
					float:none;
					
					img{
						width:100%;
						height:100%;
						object-fit:cover;
					}		
				}
				@include mobile360{
					width:300px;
					height:160px;
					border-radius:0;
					align-items:flex-start;
					float:none;
					
					img{
						width:100%;
						height:100%;
						object-fit:cover;
					}
				}
				@include mobile320{
					width:240px;
					height:140px;
					border-radius:0;
					align-items:flex-start;
					float:none;
					
					img{
						width:100%;
						height:100%;
						object-fit:cover;
					}
				}
						
			}
		}
	}
	
	/****************************************/
	.right{
		width:100%;
		height:100%;
		margin-top:2rem;
		padding-right:50px;
		flex:.5;
		position:relative;
		display:flex;
		flex-direction:column;
		overflow:hidden;
		

		@include mobile1024{
			margin-bottom:-10px;
		}
		@include mobile768{
			width:80%;
			padding-right:0px;
		}

		
		.wrapper{
			width:100%;
			height:100%;
			padding-left:50px;
			display:flex;
			flex-direction:column;
			justify-content:center;
			/*background-color:blue;*/
			

			@include mobile768{
				padding-left:5px;
				align-items:center;
				margin-left:auto;
				margin-right:auto;
			}

			
			h2{
				font-size:25px;
				font-weight:300;
				margin-bottom:2rem;
				line-height:2rem;
				
				@include mobile768{
					font-size:28px;
					margin-left:auto;
					margin-right:auto;
					flex-wrap:wrap;
					margin-bottom:30px;
				}		
				@include mobile540{
					font-size:20px;
					line-height:1.5rem;
				}		
				@include mobile414{
					font-size:18px;
					line-height:1.5rem;
				}					
				@include mobile411{
					font-size:18px;
					line-height:1.5rem;
				}				
				@include mobile375{
					font-size:18px;
					line-height:1.3rem;
				}								
				@include mobile360{
					font-size:16px;
					margin-left:auto;
					margin-right:auto;
					flex-wrap:wrap;
					margin-bottom:30px;
					line-height:1.25rem;
				}	
				@include mobile320{
					font-size:15px;
					margin-left:auto;
					margin-right:auto;
					flex-wrap:wrap;
					margin-bottom:18px;
					line-height:1rem;
				}	
			}
			
			/****************/
			h3{
				font-size:30px;
				color:$mainColor;
				margin-bottom:1.5rem;


				@include mobile768{
					font-size:25px;
					text-align:center;
				}	
				@include mobile540{
					font-size:23px;	
					margin-left:0%;
					text-align:center;					
				}				
				@include mobile414{
					font-size:20px;	
					margin-left:0%;
					text-align:center;					
				}
				@include mobile375{
					font-size:20px;	
					margin-left:0%;
					text-align:center;					
				}
				@include mobile360{
					font-size:20px;	
					margin-left:0%;
					text-align:center;					
				}
				@include mobile320{
					font-size:16px;	
					margin-left:0%;
					text-align:center;					
				}
			}
			
			span{
				font-size:1.2rem;
				color:$mainColor;
				display:inline-block;
				margin-right:1rem;
				margin-bottom:1.5rem;
				padding:.25rem .5rem;
				background-color:$mainWColor;
				border-radius:.25rem;
				
				@include mobile1024{		
					margin-bottom:0.8rem;
				}
				@include mobile540{
					font-size:.9rem;			
					margin-right:1rem;
					margin-bottom:.4rem;
				}	
				@include mobile414{
					font-size:0.8rem;			
					margin-right:1rem;
					margin-bottom:.5rem;
				}	
				@include mobile411{
					font-size:0.8rem;			
					margin-right:1rem;
					margin-bottom:.5rem;
				}				
				@include mobile360{
					font-size:0.8rem;			
					margin-right:1rem;
					margin-bottom:.5rem;
				}
				@include mobile320{
					font-size:0.6rem;			
					margin-right:1rem;
					margin-bottom:.5rem;
				}
				&:hover{
					background-color:$mainColor;
					color:$mainWColor;
				}
			}
		}
		
	}
}